@charset "utf-8";
html,
body {
  width: 100%;
  min-width: 1280px;
  height: 100%;
}
.container-box {
  width: 100%;
  height: 100%;
  background: #F0F4F9;
}
.container-box .header-box,
.container-box .main-box {
  width: 100%;
}
.container-box .header-box {
  height: 80px;
  background: linear-gradient(to right, #1F67FB, #1EA0F6);
}
.container-box .body-box {
  display: flex;
  width: 100%;
  height: calc(100% - 80px);
}
.container-box .body-box .aside-box {
  position: relative;
  height: 100%;
  background: #001F46;
}
.container-box .body-box .main-box {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  box-sizing: border-box;
}
